import React from "react";
import "../App.css";
import ImageExample from "./image.js";

function Example(props) {
  return (
    <div
      className="images"
      style={{ display: props.showExample ? "" : "none" }}
    >
      <ImageExample
        loc={require("../assests/content.jpg")}
        title={"Content Image"}
      />
      <div
        style={{
          fontSize: 50,
          fontWeight: "bold",
          alignSelf: "center",
          marginBottom: 50,
        }}
      >
        +
      </div>
      <ImageExample
        loc={require("../assests/style.jpg")}
        title={"Style Image"}
      />
      <div
        style={{
          fontSize: 50,
          fontWeight: "bold",
          alignSelf: "center",
          marginBottom: 50,
        }}
      >
        =
      </div>
      <ImageExample
        loc={require("../assests/styled.png")}
        title={"Styled Image"}
      />
    </div>
  );
}

export default Example;
