const checkIfValid = (path) => {
  if (
    path === "" ||
    path === null ||
    !(typeof path === "string" || path instanceof String)
  )
    return false;
  const ext = [
    ".jpg",
    ".jpeg",
    ".png",
    ".gif",
    ".JPG",
    ".JPEG",
    ".PNG",
    ".GIF",
  ];
  for (let i = 0; i < 4; ++i) {
    if (path.endsWith(ext[i])) return true;
  }
  return false;
};

export default checkIfValid;
