import React from "react";

function ImageExample(props) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
      }}
    >
      <img src={props.loc} alt={props.title} height="200" width="400" />
      <div style={{ marginTop: 30, alignSelf: "center", fontWeight: "bold" }}>
        {props.title}
      </div>
    </div>
  );
}

export default ImageExample;
