import React from "react";

function UploadButton(props) {
  return (
    <div>
      <label form="img">Select {props.text} Image:</label>
      <input
        type="file"
        onChange={(event) => (props.image.current = event.target.files[0])}
        id="img"
        name={props.name}
        accept="image/*"
      />
    </div>
  );
}

export default UploadButton;
